<script setup lang="ts">
import { onMounted } from "vue";
import { useEventStore } from "@/store/event/eventStore";
import { EXTENSION_EVENTS } from "@/types/Event";
import popupAlert from "@/lib/popupAlert";
import extensionApi from "./lib/extension/appExtensionApi";
import "@/lib/theme"; // Importing to initialize
import AppLayout from "@/layouts/AppLayout.vue";
import type { MessageBody } from "@/lib/extension/types";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const eventStore = useEventStore();

onMounted(async () => {
  const tab = await appExtensionApi.message.send({ event: "getCurrentTab" });
  eventStore.track({
    name: EXTENSION_EVENTS.CHECKOUT,
    data: { url: tab?.url },
  });

  // Allow other extension scripts to trigger interstitial alerts
  extensionApi.message.addListener((e: MessageBody) => {
    const { event: eventName, payload } = e;

    if (eventName === "interstitialAlert") {
      popupAlert(payload!.message, payload!.type);
    }
  });
});
</script>

<template>
  <AppLayout />
</template>
