import { createApp } from "vue";

import Interstitial from "./InterstitialApp.vue";
import router from "./router";
import store, { initAllStores } from "./store";
import { useEventStore } from "./store/event/eventStore";
import { registerLayouts } from "./layouts/register";

import "./assets/theme.scss";
import { ERROR_EVENTS, type Event } from "./types/Event";
import { CONTEXT } from "./types/App";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const app = createApp(Interstitial);
app.use(store);
app.provide("context", CONTEXT.INTERSTITIAL);

const eventStore = useEventStore();

app.config.errorHandler = async (err, vm, info) => {
  const tab = await appExtensionApi.message.send({ event: "getCurrentTab" });
  const errEvent: Event = {
    name: ERROR_EVENTS.UNHANDLED,
    data: { err, component: vm?.$options.__name, info, url: tab?.url },
  };

  try {
    await eventStore.error([errEvent]);
  } catch (err) {
    console.warn(err);
  }
};

initAllStores().then(() => {
  app.use(router);
  registerLayouts(app);

  app.mount("#app");

  const url = new URL(window.location.href);
  const params = url.searchParams;
  const query = new URLSearchParams(params.get("query") || "");

  if (params.get("page")) {
    router.push({
      name: params.get("page")!,
      query: Object.fromEntries(query),
    });
  } else if (!url.pathname.endsWith(".html")) {
    router.push({ name: "home", query: Object.fromEntries(query) });
  }
});
